body {
  font-family: "Poppins", sans-serif;
  background: #f6f7fb;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 700px;
  margin: 50px auto;
  background: white;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  color: #2d3436;
  margin-bottom: 20px;
}

.task-form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.task-form input {
  flex: 1;
  min-width: 180px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 15px;
  outline: none;
}

.task-form button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px 18px;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

.task-form button:hover {
  background-color: #43a047;
}

.task-list {
  list-style: none;
  margin-top: 25px;
  padding: 0;
}

.task-item {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  background: #fafafa;
  margin-bottom: 10px;
  padding: 15px;
  border-radius: 10px;
  transition: transform 0.2s;
}

.task-item:hover {
  transform: translateY(-2px);
}

.task-item.completed {
  background-color: #e0ffe0;
  text-decoration: none;
}

.task-info h3 {
  margin: 0;
  color: #2d3436;
}

.task-info p {
  margin: 5px 0 0;
  color: #555;
}

.task-actions button {
  margin-left: 10px;
  border: none;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: 0.3s;
}

.complete-btn {
  background-color: #2196f3;
  color: white;
}

.complete-btn:hover {
  background-color: #1976d2;
}

.delete-btn {
  background-color: #e53935;
  color: white;
}

.delete-btn:hover {
  background-color: #c62828;
}

/* Responsive Design */
@media (max-width: 600px) {
  .task-form {
    flex-direction: column;
  }

  .task-item {
    flex-direction: column;
    align-items: flex-start;
  }

  .task-actions {
    margin-top: 10px;
  }
}
